import Vue from 'vue'
import Router from 'vue-router'

const Index = resolve => require(['../views/Index'], resolve);

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    }
  ]
})



